.wrapper {
  display: grid;
  place-items: center;
}

.typing {
  width: 25ch;
  animation: typing 3s steps(25), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  font-size: 2vw;
}

@media only screen and (max-width: 800px) {
  .typing {width: 25ch;
          animation: typing 3s steps(25), blink .5s step-end infinite alternate;
          white-space: nowrap;
          overflow: hidden;
          border-right: 3px solid;
          font-family: monospace;
          font-size: 5vw;
  }
}


@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}